<template>
    <div data-v-490f8a15="" class="page">
        <div data-v-152ab910="" data-v-490f8a15="" class="sub-header">
            <div data-v-152ab910="" class="back" @click="$router.go(-1)"></div>
            <div data-v-152ab910="" class="title">绑定手机</div><!----></div>
        <div data-v-490f8a15="" class="main">
            <div data-v-490f8a15="" class="tip">
                <div data-v-490f8a15="" class="icon"></div>
                <div data-v-490f8a15="" class="text">新手机号验证后，即可绑定成功！</div>
            </div>
            <div data-v-490f8a15="" class="input-wrap" style="margin-top: 0.5rem;">
                <input data-v-490f8a15="" type="number" v-model="cellphone"
                       placeholder="绑定手机号"
                       @input="changeStyle"
                       class="cellphone"></div>
            <div data-v-490f8a15="" class="input-wrap" style="margin-top: 0.2rem;">
                <input data-v-490f8a15="" type="text"
                       placeholder="请输入短信验证码"
                       v-model="msgVcode"
                       class="code">
                <div data-v-490f8a15=""  @click="getSMSvcode" :class="{'code-btn':true ,success:isSendStyle}">{{msgText}}</div>
            </div>
            <div data-v-490f8a15="" class="save-btn" @click="onSubmit">下一步</div>
        </div>
    </div>
</template>

<script>
    import  {Toast} from  "vant"
    import Vue from "vue"
    import {mapActions} from "vuex"
    Vue.set(Toast)
    export default {
        data(){
            return {
                msgVcode:"",
                cellphone:"",
                msgText:"获取短信验证码",
                //是否显示样式success
                isSendStyle:false,

            }
        },
        mounted(){
            document.title=this.$route.meta.title;
        },
        created() {
            //安全认证
            this.$utils.safeUser(this);
            //控制 防止多次请求
            this.isSumit = true;
            //初始化定时器
            this.timers = null;
        },
        methods:{
            ...mapActions({
                //检测是否注册过手机号码
                getRegister:"user/getRegister",
                //修改手机号码
                updateCellphone:"user/updateCellphone"
            }),
            //监听手机是否正确,然后改变success的样式
            changeStyle(){
                let isChecked=true;
                if(this.cellphone.match(/^\s*$/)){
                    isChecked=false;
                }
                if(!this.cellphone.match(/^1[0-9][0-9]\d{8}$/)){
                    isChecked=false;
                }
                if(isChecked){
                    this.isSendStyle=true;
                }else{
                    this.isSendStyle=false;
                }
            },
            //检验验证码方法
            async getSMSvcode(){
                if (this.isSendStyle){
                    if(this.cellphone.match(/^\s*$/)){
                        Toast("请输入手机号码");
                        return;
                    }
                    if(!this.cellphone.match(/^1[0-9][0-9]\d{8}$/)){
                        Toast("您输入的手机号码错误");
                        return;
                    }
                    let getRegisterData = await  this.getRegister({username:this.cellphone});
                    if (getRegisterData.data.isreg === "1"){
                        Toast("您输入的号码已经被注册");
                        return;
                    }
                    this.isSendStyle = false;
                    let time = 5;
                    this.msgText = "重新获取("+time+")";
                    this.timers =setInterval(()=>{
                        if (time>0){
                            time--;
                            this.msgText = "重新获取("+time+")"
                        }else {
                            clearInterval(this.timers);
                            this.msgText = "获取短信验证码";
                            this.isSendStyle = true;
                        }
                    },1000)
                }

            },
            //修改手机号
            async onSubmit(){
                if(this.cellphone.match(/^\s*$/)){
                    Toast("请输入手机号");
                    return;
                }
                if(!this.cellphone.match(/^1[0-9][0-9]\d{8}$/)){
                    Toast("您输入的手机号格式不正确");
                    return;
                }
                let getRegisterData = await  this.getRegister({username:this.cellphone});
                if (getRegisterData.data.isreg === "1"){
                    Toast("您输入的号码已经被注册");
                    return;
                }
                if(this.msgVcode.match(/^\s*$/)){
                    Toast("请输入短信验证码");
                    return;
                }
                if (this.isSumit){
                    this.isSumit =false;
                    this.updateCellphone({cellphone:this.cellphone,vcode:this.msgVcode,success:(res)=>{
                            if (res.code ===200){
                                this.$router.go(-1)
                            }
                        }})
                }

            }

        },
        //
        beforeDestroy(){
            clearInterval(this.timers);
        }
    }
</script>

<style scoped>

    .page {
        width: 100%;
        min-height: 100vh;
        background-color: #FFFFFF;
    }

    .sub-header {
        width: 100%;
        height: 1rem;
        background-color: #FFFFFF;
        display: flex;
        display: -webkit-flex;
        align-items: center;
        -webkit-align-items: center;
        border-bottom: 1px solid #EFEFEF;
        position: fixed;
        z-index: 10;
        left: 0;
        top: 0;
        justify-content: space-between;
        padding: 0px 0.1rem;
        box-sizing: border-box
    }

    .sub-header .back {
        width: 0.8rem;
        height: 0.8rem;
        background-image: url("../../../assets/images/home/goods/back.png");
        background-size: 100%;
        background-repeat: no-repeat;
        background-position: center;
    }

    .sub-header .title {
        width: 79%;
        height: auto;
        font-size: 0.32rem;
        text-align: center;
        position: absolute;
        z-index: 1;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%)
    }

    .sub-header .right-btn {
        width: auto;
        height: auto;
        font-size: 0.32rem;
    }

    .main {
        width: 100%;
        padding-top: 1.02rem;
    }

    .main .tip {
        width: 100%;
        height: 1rem;
        background-color: #F3F5C4;
        color: #ac7700;
        font-size: 0.28rem;
        display: flex;
        display: -webkit-flex;
        align-items: center;
        -webkit-align-items: center;
    }

    .main .tip .icon {
        width: 0.4rem;
        height: 0.4rem;
        background-image: url("../../../assets/images/user/mobile/tip.png");
        background-size: 100%;
        background-repeat: no-repeat;
        background-position: center;
        margin-left: 0.4rem;
        margin-right: 0.2rem;
    }

    .main .tip .text {
        width: auto;
        height: auto;
    }

    .main .input-wrap {
        width: 93%;
        height: 0.8rem;
        border: #c5cbcf solid 1px;
        border-radius: 4px;
        margin: 0 auto;
        overflow: hidden;
    }

    .main .input-wrap .cellphone {
        width: 93%;
        height: 90%;
        font-size: 0.28rem;
        margin-left: 0.2rem;
        margin-top: 0.05rem;
    }

    .main .input-wrap .code {
        width: 69%;
        height: 90%;
        font-size: 0.28rem;
        margin-left: 0.2rem;
        margin-top: 0.05rem;
        float: left;
    }

    .main .input-wrap .code-btn {
        width: 27%;
        height: 100%;
        border-left: #EFEFEF 1px solid;
        float: left;
        font-size: 0.28rem;
        color: #717376;
        text-align: center;
        line-height: 0.8rem;
    }

    .main .input-wrap .code-btn.success {
        color: #EB1625
    }

    .main .save-btn {
        width: 93%;
        height: 0.8rem;
        background-color: #E42321;
        font-size: 0.28rem;
        color: #FFFFFF;
        text-align: center;
        line-height: 0.8rem;
        margin: 0 auto;
        border-radius: 4px;
        margin-top: 0.2rem;
    }
</style>